@media screen and (max-width: 1100px){
    /* setting for header */
    .mainContainer{
        max-width: 800px;
    }

    .headerContainer{
        grid-template-columns: 1fr 2fr 1fr;
    }

    /* settings for banner section*/
    .bannerLatest{
        width: 170px;

    }
    .bannerContentHeader{
        font-size: 25px;
        width: 200px;
    }
    .bannerBottomText{
        bottom: 15px;
        left: 15px;
        font-size: 16px;
    }
}

@media screen and (max-width: 800px){
    /* header setting */
    .menuBar{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .headerNav, .headerSocialMedia{
        display: none;
    }
    .headerContainer{
        grid-template-columns: 1fr 11fr;
        padding: 40px 0;
    }

    /* settings for banner section*/
    .bannerLatest{
        width: 160px;
    }
    .bannerContentHeader{
        font-size: 18px;
        width: 180px;
    }

    /* settings for blog section*/
    .blogImgDesc{
        width: 100px;
        height: 90px;
        font-size: 20px;

    }
    .blogDescContainer{
        margin-top: 70px;
        font-size: 14px;
    }
    .blogImgDescContainer{
        bottom: -45px;
    }

    /* settings for insta section*/

    .insta{
        grid-template-columns: repeat(2, 1fr);
    }

}

@media screen and (max-width: 700px){

    /* settings for banner section*/
    .bannerContentHeader{
        font-size: 16px;
        width: 180px;
    }
    .bannerLatest{
        width: 120px;
    }
    .bannerBottomText{
        font-size: 13px;
    }

    /* settings for intra section*/
    .intra{
        grid-template-columns: 1fr;
        padding: 32px;
    }

}

@media screen and (max-width: 600px){

    /* setting for header */
    .headerContainer{
        grid-template-columns: 1fr 5fr;
        padding: 30px 0;
    }
    /* settings for banner section*/
    .banner{
        grid-template-columns: 1fr;
    }
    .bannerContent{
        padding-left: 0;
        grid-template-areas: 'header'
        'latest'
        'insta';
        grid-gap: 8px 0;

    }
    .bannerContentHeader{
        width: 100%;
        margin-top: 30px;

    }
    .bannerLatest{
        width: 100%;
    }

    .bannerBottomText{
        bottom: 20px;
        left: 25px;
        font-size: 28px;
    }

    /* settings for subscribe section*/
    .subscribeForm{
        display: flex;
        flex-direction: column;
        align-items: center;

    }
    .subscribeBtn{
        margin-top: 20px;
        width: 40%;
        padding: 10px 5px;
        letter-spacing: normal;
        font-size: 13px;

    }
    .subscribeHeader{
        font-size: 26px;
    }
    .subscribeInput{
        height: 40px;
    }
    .subscribeMail{
        width: 220px;
    }

    /* settings for blog section*/
    .blog{
        grid-template-columns: 1fr;
    }
    .blogItemDesc{
        width: 140px;
        height: 120px;
        font-size: 24px;
    }
    .blogDescContainer{
        margin-top: 70px;
        font-size: 14px;
    }
    .blogImgDescContainer{
        bottom: -60px;
    }

    /* settings for insta section*/
    .instaItemDescription{
        padding: 8px;
        font-size: 22px;

    }
    .instaItem{
        padding: 16px;
    }

}































