body{
    margin: 0;
    padding: 0;
    font-family: 'Roboto', 'sans-serif';
}

.mainContainer{
    margin: 0 auto;
    max-width: 1120px;
    min-width: 320px;
}

/* header  styles*/

.menuBar{
    display: none;
}

.headerContainer{
    width: 100%;
    padding: 70px 32px;
    display: grid;
    grid-template-columns: 1fr 3fr 1fr;
    box-sizing: border-box;
}
.mainLogo{
    width: 163px;
}

.headerNav{
    display: flex;
    align-items: center;
}

.headerLink{
    margin: 0 7px;
    text-decoration: none;
    color: #000;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 11px;
    letter-spacing: 3px;
}

.headerLink:hover{
    color: #4c665b;
}

.headerSocialMedia{
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.headerSocialIcon{
    margin: 0 8px;
}

.headerSocialMedia:hover{
    /*color: #4c665b;*/
    opacity: 1;
}

.headerSocialIcon:hover{
    /*color: #000;*/
    opacity: 0.5;
}


/* styles for banner section*/

.banner{
    display: grid;
    grid-template-columns: 5fr 6fr;
    align-items: end;
    padding: 32px;
}

.bannerImg{
    width:100%;

}

.bannerContent{
    display: grid;
    padding-left: 30px;
    grid-template-areas: 'header .' 'latest insta';
}

.bannerContentImgContainer{
    display: flex;
    align-items: flex-end;
}

.bannerContentHeader{
    font-size: 36px;
    width: 280px;
    border-top: 3px solid #000;
    border-bottom: 3px solid #000;
    padding: 10px 0;
    text-transform: uppercase;
    grid-area: header;
}

.bannerLatest{
    width: 230px;

}
.bannerLatestContainer{
    grid-area: latest;
}

.bannerInstaContainer{
    grid-area: insta;
}

.bannerContentImgContainer{
    position: relative;
    display: flex;
    align-items: flex-end;
}

.bannerBottomText{
    position: absolute;
    left: 30px;
    bottom: 25px;
    font-size: 20px;
    font-weight: bold;
    color: #fff;
    text-transform: uppercase;
    text-decoration: none;
}

/* styles for subscribe section*/
.subscribe{
    margin: 32px;
    background-color: #4c665b;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px 0 50px;

}

.subscribeHeader{
    color: #fff;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: 30px;
}

.subscribeInput{
    height: 50px;
    box-sizing: border-box;
    padding: 15px;

}

.subscribeEmail{
    width: 240px;

}
.subscribeBtn{
    width: 170px;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 2px;
    background-color: #ebefee;
    border: none;
    cursor: pointer;
}

/* styles for intra section*/
.intra{
    padding: 32px 72px;
    display: grid;
    grid-template-columns: 4fr 6fr;
}
.introHeader{
    text-transform: uppercase;
    font-size: 32px;
    margin-top: 16px;
}
.introText{
    font-weight: lighter;
    text-align: justify;
}


/* styles for blog section*/

.blog{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}
.blogItem{
    padding: 24px;

}
.blogImg{
    width: 100%
}
.blogImgContainer{
    position: relative;
}
.blogImgDescContainer{
    position: absolute;
    bottom: -70px;
    display: flex;
    justify-content: center;
    width: 100%;
}
.blogImgDesc{
    width: 150px;
    height: 140px;
    background-color: #ecf0ef;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 26px;
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
}

.underline{
    background-color: #000;
    width: 45px;
    height: 2px;
    margin: 3px auto;
    border: none;
}
.blogDescContainer{
    margin-top: 90px;
    font-weight: lighter;
    letter-spacing: 1px;
    text-align: justify;
}

/* styles for insta section */

.insta{
    display: grid;
    grid-template-columns: repeat(4, 1fr);

}
.instaItem{
    padding: 24px;

}
.instaItemImg{
    width: 100%;
}
.instaItemDescription{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 30px;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 2px;
    padding: 24px;
}

/* styles for footer section */

.footerContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    border-top: 2px solid #000;
    padding: 40px;
}
.footerlogo{
    width: 140px;
    margin-bottom: 40px;
}
.footerCopyright{
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 2px;
}








